t-2 !default;\n$layout-spacing-sm: $unit-1 !default;\n$layout-spacing-lg: $unit-4 !default;\n$border-radius: $unit-h !default;\n$border-width: $unit-o !default;\n$border-width-lg: $unit-h !default;\n$control-size: $unit-9 !default;\n$control-size-sm: $unit-7 !default;\n$control-size-lg: $unit-10 !default;\n$control-padding-x: $unit-2 !default;\n$control-padding-x-sm: $unit-2 * .75 !default;\n$control-padding-x-lg: $unit-2 * 1.5 !default;\n$control-padding-y: ($control-size - $line-height) / 2 - $border-width !default;\n$control-padding-y-sm: ($control-size-sm - $line-height) / 2 - $border-width !default;\n$control-padding-y-lg: ($control-size-lg - $line-height) / 2 - $border-width !default;\n$control-icon-size: .8rem !default;\n\n$control-width-xs: 180px !default;\n$control-width-sm: 320px !default;\n$control-width-md: 640px !default;\n$control-width-lg: 960px !default;\n$control-width-xl: 1280px !default;\n\n// Responsive breakpoints\n$size-xs: 480px !default;\n$size-sm: 600px !default;\n$size-md: 840px !default;\n$size-lg: 960px !default;\n$size-xl: 1280px !default;\n$size-2x: 1440px !default;\n\n$responsive-breakpoint: $size-xs !default;\n\n// Z-index\n$zindex-0: 1 !default;\n$zindex-1: 100 !default;\n$zindex-2: 200 !default;\n$zindex-3: 300 !default;\n$zindex-4: 400 !default;\n","// Base\n*,\n*::before,\n*::after {\n  box-sizing: inherit;\n}\n\nhtml {\n  box-sizing: border-box;\n  font-size: $html-font-size;\n  line-height: $html-line-height;\n  -webkit-tap-highlight-color: transparent;\n}\n\nbody {\n  background: $body-bg;\n  color: $body-font-color;\n  font-family: $body-font-family;\n  font-size: $font-size;\n  overflow-x: hidden;\n  text-rendering: optimizeLegibility;\n}\n\na {\n  color: $link-color;\n  outline: none;\n  text-decoration: none;\n\n  &:focus {\n    @include control-shadow();\n  }\n\n  &:focus,\n  &:hover,\n  &:active,\n  &.active {\n    color: $link-color-dark;\n    text-decoration: underline;\n  }\n\n  &:visited {\n    color: $link-color-light;\n  }\n}\n","//* changing color for Spectre.css\n$primary-color:#1A5276;\n// $dark-color: #1B2631;\n\n//* projects variables\n$bg-color: ghostwhite;","// Component focus shadow\n@mixin control-shadow($color: $primary-color) {\n  box-shadow: 0 0 0 .1rem rgba($color, .2);\n}\n\n// Shadow mixin\n@mixin shadow-variant($offset) {\n  box-shadow: 0 $offset ($offset + .05rem) * 2 rgba($dark-color, .3);\n}\n","// Typography\n// Headings\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  color: inherit;\n  font-weight: 500;\n  line-height: 1.2;\n  margin-bottom: .5em;\n  margin-top: 0;\n}\n.h1,\n.h2,\n.h3,\n.h4,\n.h5,\n.h6 {\n  font-weight: 500;\n}\nh1,\n.h1 {\n  font-size: 2rem;\n}\nh2,\n.h2 {\n  font-size: 1.6rem;\n}\nh3,\n.h3 {\n  font-size: 1.4rem;\n}\nh4,\n.h4 {\n  font-size: 1.2rem;\n}\nh5,\n.h5 {\n  font-size: 1rem;\n}\nh6,\n.h6 {\n  font-size: .8rem;\n}\n\n// Paragraphs\np {\n  margin: 0 0 $line-height;\n}\n\n// Semantic text elements\na,\nins,\nu {\n  text-decoration-skip: ink edges;\n}\n\nabbr[title] {\n  border-bottom: $border-width dotted;\n  cursor: help;\n  text-decoration: none;\n}\n\nkbd {\n  @include label-base();\n  @include label-variant($light-color, $dark-color);\n  font-size: $font-size-sm;\n}\n\nmark {\n  @include label-variant($body-font-color, $highlight-color);\n  border-bottom: $unit-o solid darken($highlight-color, 15%);\n  border-radius: $border-radius;\n  padding: $unit-o $unit-h 0;\n}\n\n// Blockquote\nblockquote {\n  border-left: $border-width-lg solid $border-color;\n  margin-left: 0;\n  padding: $unit-2 $unit-4;\n\n  p:last-child {\n    margin-bottom: 0;\n  }\n}\n\n// Lists\nul,\nol {\n  margin: $unit-4 0 $unit-4 $unit-4;\n  padding: 0;\n\n  ul,\n  ol {\n    margin: $unit-4 0 $unit-4 $unit-4;\n  }\n\n  li {\n    margin-top: $unit-2;\n  }\n}\n\nul {\n  list-style: disc inside;\n\n  ul {\n    list-style-type: circle;\n  }\n}\n\nol {\n  list-style: decimal inside;\n\n  ol {\n    list-style-type: lower-alpha;\n  }\n}\n\ndl {\n  dt {\n    font-weight: bold;\n  }\n  dd {\n    margin: $unit-2 0 $unit-4 0;\n  }\n}\n","// Label base style\n@mixin label-base() {\n  border-radius: $border-radius;\n  line-height: 1.25;\n  padding: .1rem .2rem;\n}\n\n@mixin label-variant($color: $light-color, $bg-color: $primary-color) {\n  background: $bg-color;\n  color: $color;\n}\n","// Optimized for East Asian CJK\nhtml:lang(zh),\nhtml:lang(zh-Hans),\n.lang-zh,\n.lang-zh-hans {\n  font-family: $cjk-zh-hans-font-family;\n}\n\nhtml:lang(zh-Hant),\n.lang-zh-hant {\n  font-family: $cjk-zh-hant-font-family;\n}\n\nhtml:lang(ja),\n.lang-ja {\n  font-family: $cjk-jp-font-family;\n}\n\nhtml:lang(ko),\n.lang-ko {\n  font-family: $cjk-ko-font-family;\n}\n\n:lang(zh),\n:lang(ja),\n.lang-cjk {\n  ins,\n  u {\n    border-bottom: $border-width solid;\n    text-decoration: none;\n  }\n\n  del + del,\n  del + s,\n  ins + ins,\n  ins + u,\n  s + del,\n  s + s,\n  u + ins,\n  u + u {\n    margin-left: .125em;\n  }\n}\n","// Tables\n.table {\n  border-collapse: collapse;\n  border-spacing: 0;\n  width: 100%;\n  @if $rtl == true {\n    text-align: right;\n  } @else {\n    text-align: left;\n  }\n\n  &.table-striped {\n    tbody {\n      tr:nth-of-type(odd) {\n        background: $bg-color;\n      }\n    }\n  }\n\n  &,\n  &.table-striped {\n    tbody {\n      tr {\n        &.active {\n          background: $bg-color-dark;\n        }\n      }\n    }\n  }\n\n  &.table-hover {\n    tbody {\n      tr {\n        &:hover {\n          background: $bg-color-dark;\n        }\n      }\n    }\n  }\n\n  // Scollable tables\n  &.table-scroll {\n    display: block;\n    overflow-x: auto;\n    padding-bottom: .75rem;\n    white-space: nowrap;\n  }\n\n  td,\n  th {\n    border-bottom: $border-width solid $border-color;\n    padding: $unit-3 $unit-2;\n  }\n  th {\n    border-bottom-width: $border-width-lg;\n  }\n}\n","// Buttons\n.btn {\n  appearance: none;\n  background: $bg-color-light;\n  border: $border-width solid $primary-color;\n  border-radius: $border-radius;\n  color: $primary-color;\n  cursor: pointer;\n  display: inline-block;\n  font-size: $font-size;\n  height: $control-size;\n  line-height: $line-height;\n  outline: none;\n  padding: $control-padding-y $control-padding-x;\n  text-align: center;\n  text-decoration: none;\n  transition: background .2s, border .2s, box-shadow .2s, color .2s;\n  user-select: none;\n  vertical-align: middle;\n  white-space: nowrap;\n  &:focus {\n    @include control-shadow();\n  }\n  &:focus,\n  &:hover {\n    background: $secondary-color;\n    border-color: $primary-color-dark;\n    text-decoration: none;\n  }\n  &:active,\n  &.active {\n    background: $primary-color-dark;\n    border-color: darken($primary-color-dark, 5%);\n    color: $light-color;\n    text-decoration: none;\n    &.loading {\n      &::after {\n        border-bottom-color: $light-color;\n        border-left-color: $light-color;\n      }\n    }\n  }\n  &[disabled],\n  &:disabled,\n  &.disabled {\n    cursor: default;\n    opacity: .5;\n    pointer-events: none;\n  }\n\n  // Button Primary\n  &.btn-primary {\n    background: $primary-color;\n    border-color: $primary-color-dark;\n    color: $light-color;\n    &:focus,\n    &:hover {\n      background: darken($primary-color-dark, 2%);\n      border-color: darken($primary-color-dark, 5%);\n      color: $light-color;\n    }\n    &:active,\n    &.active {\n      background: darken($primary-color-dark, 4%);\n      border-color: darken($primary-color-dark, 7%);\n      color: $light-color;\n    }\n    &.loading {\n      &::after {\n        border-bottom-color: $light-color;\n        border-left-color: $light-color;\n      }\n    }\n  }\n\n  // Button Colors\n  &.btn-success {\n    @include button-variant($success-color);\n  }\n\n  &.btn-error {\n    @include button-variant($error-color);\n  }\n\n  // Button Link\n  &.btn-link {\n    background: transparent;\n    border-color: transparent;\n    color: $link-color;\n    &:focus,\n    &:hover,\n    &:active,\n    &.active {\n      color: $link-color-dark;\n    }\n  }\n\n  // Button Sizes\n  &.btn-sm {\n    font-size: $font-size-sm;\n    height: $control-size-sm;\n    padding: $control-padding-y-sm $control-padding-x-sm;\n  }\n\n  &.btn-lg {\n    font-size: $font-size-lg;\n    height: $control-size-lg;\n    padding: $control-padding-y-lg $control-padding-x-lg;\n  }\n\n  // Button Block\n  &.btn-block {\n    display: block;\n    width: 100%;\n  }\n\n  // Button Action\n  &.btn-action {\n    width: $control-size;\n    padding-left: 0;\n    padding-right: 0;\n\n    &.btn-sm {\n      width: $control-size-sm;\n    }\n\n    &.btn-lg {\n      width: $control-size-lg;\n    }\n  }\n\n  // Button Clear\n  &.btn-clear {\n    background: transparent;\n    border: 0;\n    color: currentColor;\n    height: $unit-5;\n    line-height: $unit-4;\n    margin-left: $unit-1;\n    margin-right: -2px;\n    opacity: 1;\n    padding: $unit-h;\n    text-decoration: none;\n    width: $unit-5;\n\n    &:focus,\n    &:hover {\n      background: rgba($bg-color, .5);\n      opacity: .95;\n    }\n\n    &::before {\n      content: \"\\2715\";\n    }\n  }\n}\n\n// Button groups\n.btn-group {\n  display: inline-flex;\n  flex-wrap: wrap;\n\n  .btn {\n    flex: 1 0 auto;\n    &:first-child:not(:last-child) {\n      border-bottom-right-radius: 0;\n      border-top-right-radius: 0;\n    }\n    &:not(:first-child):not(:last-child) {\n      border-radius: 0;\n      margin-left: -$border-width;\n    }\n    &:last-child:not(:first-child) {\n      border-bottom-left-radius: 0;\n      border-top-left-radius: 0;\n      margin-left: -$border-width;\n    }\n    &:focus,\n    &:hover,\n    &:active,\n    &.active {\n      z-index: $zindex-0;\n    }\n  }\n\n  &.btn-group-block {\n    display: flex;\n\n    .btn {\n      flex: 1 0 0;\n    }\n  }\n}\n","// Button variant mixin\n@mixin button-variant($color: $primary-color) {\n  background: $color;\n  border-color: darken($color, 3%);\n  color: $light-color;\n  &:focus {\n    @include control-shadow($color);\n  }\n  &:focus,\n  &:hover {\n    background: darken($color, 2%);\n    border-color: darken($color, 5%);\n    color: $light-color;\n  }\n  &:active,\n  &.active {\n    background: darken($color, 7%);\n    border-color: darken($color, 10%);\n    color: $light-color;\n  }\n  &.loading {\n    &::after {\n      border-bottom-color: $light-color;\n      border-left-color: $light-color;\n    }\n  }\n}\n\n@mixin button-outline-variant($color: $primary-color) {\n  background: $light-color;\n  border-color: $color;\n  color: $color;\n  &:focus {\n    @include control-shadow($color);\n  }\n  &:focus,\n  &:hover {\n    background: lighten($color, 50%);\n    border-color: darken($color, 2%);\n    color: $color;\n  }\n  &:active,\n  &.active {\n    background: $color;\n    border-color: darken($color, 5%);\n    color: $light-color;\n  }\n  &.loading {\n    &::after {\n      border-bottom-color: $color;\n      border-left-color: $color;\n    }\n  }\n}\n","// Forms\n.form-group {\n  &:not(:last-child) {\n    margin-bottom: $layout-spacing;\n  }\n}\n\nfieldset {\n  margin-bottom: $layout-spacing-lg;\n}\n\nlegend {\n  font-size: $font-size-lg;\n  font-weight: 500;\n  margin-bottom: $layout-spacing-lg;\n}\n\n// Form element: Label\n.form-label {\n  display: block;\n  line-height: $line-height;\n  padding: $control-padding-y + $border-width 0;\n\n  &.label-sm {\n    font-size: $font-size-sm;\n    padding: $control-padding-y-sm + $border-width 0;\n  }\n\n  &.label-lg {\n    font-size: $font-size-lg;\n    padding: $control-padding-y-lg + $border-width 0;\n  }\n}\n\n// Form element: Input\n.form-input {\n  appearance: none;\n  background: $bg-color-light;\n  background-image: none;\n  border: $border-width solid $border-color-dark;\n  border-radius: $border-radius;\n  color: $body-font-color;\n  display: block;\n  font-size: $font-size;\n  height: $control-size;\n  line-height: $line-height;\n  max-width: 100%;\n  outline: none;\n  padding: $control-padding-y $control-padding-x;\n  