-body {\n    padding: $layout-spacing;\n  }\n}","// Navbar\n.navbar {\n  align-items: stretch;\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-between;\n\n  .navbar-section {\n    align-items: center;\n    display: flex;\n    flex: 1 0 0;\n\n    &:not(:first-child):last-child {\n      justify-content: flex-end;\n    }\n  }\n\n  .navbar-center {\n    align-items: center;\n    display: flex;\n    flex: 0 0 auto;\n  }\n\n  .navbar-brand {\n    font-size: $font-size-lg;\n    text-decoration: none;\n  }\n}\n","// Accordions\n.accordion {\n  input:checked ~,\n  &[open] {\n    & .accordion-header > {\n      .icon:first-child {\n        transform: rotate(90deg);\n      }\n    }\n\n    & .accordion-body {\n      max-height: 50rem;\n    }\n  }\n\n  .accordion-header {\n    display: block;\n    padding: $unit-1 $unit-2;\n\n    .icon {\n      transition: transform .25s;\n    }\n  }\n\n  .accordion-body {\n    margin-bottom: $layout-spacing;\n    max-height: 0;\n    overflow: hidden;\n    transition: max-height .25s;\n  }\n}\n\n// Remove default details marker in Webkit\nsummary.accordion-header {\n  &::-webkit-details-marker {\n    display: none;\n  }\n}\n","// Avatars\n.avatar {\n  @include avatar-base();\n  background: $primary-color;\n  border-radius: 50%;\n  color: rgba($light-color, .85);\n  display: inline-block;\n  font-weight: 300;\n  line-height: 1.25;\n  margin: 0;\n  position: relative;\n  vertical-align: middle;\n\n  &.avatar-xs {\n    @include avatar-base($unit-4);\n  }\n  &.avatar-sm {\n    @include avatar-base($unit-6);\n  }\n  &.avatar-lg {\n    @include avatar-base($unit-12);\n  }\n  &.avatar-xl {\n    @include avatar-base($unit-16);\n  }\n\n  img {\n    border-radius: 50%;\n    height: 100%;\n    position: relative;\n    width: 100%;\n    z-index: $zindex-0;\n  }\n\n  .avatar-icon,\n  .avatar-presence {\n    background: $bg-color-light;\n    bottom: 14.64%;\n    height: 50%;\n    padding: $border-width-lg;\n    position: absolute;\n    right: 14.64%;\n    transform: translate(50%, 50%);\n    width: 50%;\n    z-index: $zindex-0 + 1;\n  }\n\n  .avatar-presence {\n    background: $gray-color;\n    box-shadow: 0 0 0 $border-width-lg $light-color;\n    border-radius: 50%;\n    height: .5em;\n    width: .5em;\n\n    &.online {\n      background: $success-color;\n    }\n\n    &.busy {\n      background: $error-color;\n    }\n\n    &.away {\n      background: $warning-color;\n    }\n  }\n\n  &[data-initial]::before {\n    color: currentColor;\n    content: attr(data-initial);\n    left: 50%;\n    position: absolute;\n    top: 50%;\n    transform: translate(-50%, -50%);\n    z-index: $zindex-0;\n  }\n}","// Avatar mixin\n@mixin avatar-base($size: $unit-8) {\n  font-size: $size / 2;\n  height: $size;\n  width: $size;\n}\n","// Badges\n.badge {\n  position: relative;\n  white-space: nowrap;\n\n  &[data-badge],\n  &:not([data-badge]) {\n    &::after {\n      background: $primary-color;\n      background-clip: padding-box;\n      border-radius: .5rem;\n      box-shadow: 0 0 0 .1rem $bg-color-light;\n      color: $light-color;\n      content: attr(data-badge);\n      display: inline-block;\n      transform: translate(-.05rem, -.5rem);\n    }\n  }\n  &[data-badge] {\n    &::after {\n      font-size: $font-size-sm;\n      height: .9rem;\n      line-height: 1;\n      min-width: .9rem;\n      padding: .1rem .2rem;\n      text-align: center;\n      white-space: nowrap;\n    }\n  }\n  &:not([data-badge]),\n  &[data-badge=\"\"] {\n    &::after {\n      height: 6px;\n      min-width: 6px;\n      padding: 0;\n      width: 6px;\n    }\n  }\n\n  // Badges for Buttons\n  &.btn {\n    &::after {\n      position: absolute;\n      top: 0;\n      right: 0;\n      transform: translate(50%, -50%);\n    }\n  }\n\n  // Badges for Avatars\n  &.avatar {\n    &::after {\n      position: absolute;\n      top: 14.64%;\n      right: 14.64%;\n      transform: translate(50%, -50%);\n      z-index: $zindex-1;\n    }\n  }\n}\n","// Breadcrumbs\n.breadcrumb {\n  list-style: none;\n  margin: $unit-1 0;\n  padding: $unit-1 0;\n\n  .breadcrumb-item {\n    color: $gray-color-dark;\n    display: inline-block;\n    margin: 0;\n    padding: $unit-1 0;\n\n    &:not(:last-child) {\n      margin-right: $unit-1;\n\n      a {\n        color: $gray-color-dark;\n      }\n    }\n\n    &:not(:first-child) {\n      &::before {\n        color: $gray-color-dark;\n        content: \"/\";\n        padding-right: $unit-2;\n      }\n    }\n  }\n}\n","// Bars\n.bar {\n  background: $bg-color-dark;\n  border-radius: $border-radius;\n  display: flex;\n  flex-wrap: nowrap;\n  height: $unit-4;\n  width: 100%;\n\n  &.bar-sm {\n    height: $unit-1;\n  }\n\n  // TODO: attr() support\n  .bar-item {\n    background: $primary-color;\n    color: $light-color;\n    display: block;\n    font-size: $font-size-sm;\n    flex-shrink: 0;\n    line-height: $unit-4;\n    height: 100%;\n    position: relative;\n    text-align: center;\n    width: 0;\n\n    &:first-child {\n      border-bottom-left-radius: $border-radius;\n      border-top-left-radius: $border-radius;\n    }\n    &:last-child {\n      border-bottom-right-radius: $border-radius;\n      border-top-right-radius: $border-radius;\n      flex-shrink: 1;\n    }\n  }\n}\n\n// Slider bar\n.bar-slider {\n  height: $border-width-lg;\n  margin: $layout-spacing 0;\n  position: relative;\n\n  .bar-item {\n    left: 0;\n    padding: 0;\n    position: absolute;\n    &:not(:last-child):first-child {\n      background: $bg-color-dark;\n      z-index: $zindex-0;\n    }\n  }\n\n  .bar-slider-btn {\n    background: $primary-color;\n    border: 0;\n    border-radius: 50%;\n    height: $unit-3;\n    padding: 0;\n    position: absolute;\n    right: 0;\n    top: 50%;\n    transform: translate(50%, -50%);\n    width: $unit-3;\n\n    &:active {\n      box-shadow: 0 0 0 .1rem $primary-color;\n    }\n  }\n}\n","// Cards\n.card {\n  background: $bg-color-light;\n  border: $border-width solid $border-color;\n  border-radius: $border-radius;\n  display: flex;\n  flex-direction: column;\n\n  .card-header,\n  .card-body,\n  .card-footer {\n    padding: $layout-spacing-lg;\n    padding-bottom: 0;\n\n    &:last-child {\n      padding-bottom: $layout-spacing-lg;\n    }\n  }\n\n  .card-body {\n    flex: 1 1 auto;\n  }\n\n  .card-image {\n    padding-top: $layout-spacing-lg;\n\n    &:first-child {\n      padding-top: 0;\n\n      img {\n        border-top-left-radius: $border-radius;\n        border-top-right-radius: $border-radius;\n      }\n    }\n\n    &:last-child {\n      img {\n        border-bottom-left-radius: $border-radius;\n        border-bottom-right-radius: $border-radius;\n      }\n    }\n  }\n}\n","// Chips\n.chip {\n  align-items: center;\n  background: $bg-color-dark;\n  border-radius: 5rem;\n  display: inline-flex;\n  font-size: 90%;\n  height: $unit-6;\n  line-height: $unit-4;\n  margin: $unit-h;\n  max-width: $control-width-sm;\n  overflow: hidden;\n  padding: $unit-1 $unit-2;\n  text-decoration: none;\n  text-overflow: ellipsis;\n  vertical-align: middle;\n  white-space: nowrap;\n\n  &.active {\n    background: $primary-color;\n    color: $light-color;\n  }\n\n  .avatar {\n    margin-left: -$unit-2;\n    margin-right: $unit-1;\n  }\n\n  .btn-clear {\n    border-radius: 50%;\n    transform: scale(.75);\n  }\n}\n","// Dropdown\n.dropdown {\n  display: inline-block;\n  position: relative;\n\n  .menu {\n    animation: slide-down .15s ease 1;\n    display: none;\n    left: 0;\n    max-height: 50vh;\n    overflow-y: auto;\n    position: absolute;\n    top: 100%;\n  }\n\n  &.dropdown-right {\n    .menu {\n      left: auto;\n      right: 0;\n    }\n  }\n\n  &.active .menu,\n  .dropdown-toggle:focus + .menu,\n  .menu:hover {\n    display: block;\n  }\n\n  // Fix dropdown-toggle border radius in button groups\n  .btn-group {\n    .dropdown-toggle:nth-last-child(2) {\n      border-bottom-right-radius: $border-radius;\n      border-top-right-radius: $border-radius;\n    }\n  }\n}\n","// Empty states (or Blank slates)\n.empty {\n  background: $bg-color;\n  border-radius: $border-radius;\n  color: $gray-color-dark;\n  text-align: center;\n  padding: $unit-16 $unit-8;\n\n  .empty-icon {\n    margin-bottom: $layout-spacing-lg;\n  }\n\n  .empty-title,\n  .empty-subtitle {\n    margin: $layout-spacing auto;\n  }\n\n  .empty-action {\n    margin-top: $layout-spacing-lg;\n  }\n}\n","// Menus\n.menu {\n  @include shadow-variant(.05rem);\n  background: $bg-color-light;\n  border-radius: $border-radius;\n  list-style: none;\n  margin: 0;\n  min-width: $control-width-xs;\n  padding: $unit-2;\n  transform: translateY($layout-spacing-sm);\n  z-index: $zindex-3;\n\n  &.menu-nav {\n    background: transparent;\n    box-shadow: none;\n  }\n\n  .menu-item {\n    margin-top: 0;\n    padding: 0 $unit-2;\n    position: relative;\n    text-decoration: none;\n\n    & > a {\n      border-radius: $border-radius;\n      color: inherit;\n      display: block;\n      margin: 0 (-$unit-2);\n      padding: $unit-1 $unit-2;\n      text-decoration: none;\n      &:focus,\n      &:hover {\n        background: $secondary-color;\n        color: $primary-color;\n      }\n      &:active,\n      &.active {\n        background: $secondary-color;\n        color: $primary-color;\n      }\n    }\n\n    .form-checkbox,\n    .form-radio,\n    .form-switch {\n      margin: $unit-h 0;\n    }\n\n    & + .menu-item {\n      margin-top: $unit-1;\n    }\n  }\n\n  .menu-badge {\n    align-items: center;\n    display: flex;\n    height: 100%;\n    position: absolute;\n    right: 0;\n    top: 0;\n\n    .label {\n      margin-right: $unit-2;\n    }\n  }\n}","// Modals\n.modal {\n  align-items: center;\n  bottom: 0;\n  display: none;\n  justify-content: center;\n  left: 0;\n  opacity: 0;\n  overflow: hidden;\n  padding: $layout-spacing;\n  position: fixed;\n  right: 0;\n  top: 0;\n\n  &:target,\n  &.active {\n    display: flex;\n    opacity: 1;\n    z-index: $zindex-4;\n\n    .modal-overlay {\n      background: rgba($bg-color, .75);\n      bottom: 0;\n      cursor: default;\n      display: block;\n      left: 0;\n      position: absolute;\n      right: 0;\n      top: 0;\n    }\n\n    .modal-container {\n      animation: slide-down .2s ease 1;\n      z-index: $zindex-0;\n    }\n  }\n\n  &.modal-sm {\n    .modal-container {\n      max-width: $control-width-sm;\n      padding: 0 $unit-2;\n    }\n  }\n\n  &.modal-lg {\n    .modal-overlay {\n      background: $bg-color-light;\n    }\n\n    .modal-container {\n      box-shadow: none;\n      max-width: $control-width-lg;\n    }\n  }\n}\n\n.modal-container {\n  @include shadow-variant(.2rem);\n  background: $bg-color-light;\n  border-radius: $border-radius;\n  display: flex;\n  flex-direction: column;\n  max-height: 75vh;\n  max-width: $control-width-md;\n  padding: 0 $unit-4;\n  width: 100%;\n\n  &.modal-fullheight {\n    max-height: 100vh;\n  }\n\n  .modal-header {\n    color: $dark-color;\n    padding: $unit-4;\n  }\n\n  .modal-body {\n    overflow-y: auto;\n    padding: $unit-4;\n    position: relative;\n  }\n\n  .modal-footer {\n    padding: $unit-4;\n    text-align: right;\n  }\n}\n","// Navs\n.nav {\n  display: flex;\n  flex-direction: column;\n  list-style: none;\n  margin: $unit-1 0;\n\n  .nav-item {\n    a {\n      color: $gray-color-dark;\n      padding: $unit-1 $unit-2;\n      text-decoration: none;\n      &:focus,\n      &:hover {\n        color: $primary-color;\n      }\n    }\n    &.active {\n      & > a {\n        color: darken($gray-color-dark, 10%);\n        font-weight: bold;\n        &:focus,\n        &:hover {\n          color: $primary-color;\n        }\n      }\n    }\n  }\n\n  & .nav {\n    margin-bottom: $unit-2;\n    margin-left: $unit-4;\n  }\n}\n","// Pagination\n.pagination {\n  display: flex;\n  list-style: none;\n  margin: $unit-1 0;\n  padding: $unit-1 0;\n\n  .page-item {\n    margin: $unit-1 $unit-o;\n\n    span {\n      display: inline-block;\n      padding: $unit-1 $unit-1;\n    }\n\n    a {\n      border-radius: $border-radius;\n      display: inline-block;\n      padding: $unit-1 $unit-2;\n      text-decoration: none;\n      &:focus,\n      &:hover {\n        color: $primary-color;\n      }\n    }\n\n    &.disabled {\n      a {\n        cursor: default;\n        opacity: .5;\n        pointer-events: none;\n      }\n    }\n\n    &.active {\n      a {\n        background: $primary-color;\n        color: $light-color;\n      }\n    }\n\n    &.page-prev,\n    &.page-next {\n      flex: 1 0 50%;\n    }\n\n    &.page-next {\n      text-align: right;\n    }\n\n    .page-item-title {\n      margin: 0;\n    }\n\n    .page-item-subtitle {\n      margin: 0;\n      opacity: .5;\n    }\n  }\n}\n","// Panels\n.panel {\n  border: $border-width solid $border-color;\n  border-radius: $border-radius;\n  display: flex;\n  flex-direction: column;\n\n  .panel-header,\n  .panel-footer {\n    flex: 0 0 auto;\n    padding: $layout-spacing-lg;\n  }\n\n  .panel-nav {\n    flex: 0 0 auto;\n  }\n\n  .panel-body {\n    flex: 1 1 auto;\n    overflow-y: auto;\n    padding: 0 $layout-spacing-lg;\n  }\n}\n","// Popovers\n.popover {\n  display: inline-block;\n  position: relative;\n\n  .popover-container {\n    left: 50%;\n    opacity: 0;\n    padding: $layout-spacing;\n    position: absolute;\n    top: 0;\n    transform: translate(-50%, -50%) scale(0);\n    transition: transform .2s;\n    width: $control-width-sm;\n    z-index: $zindex-3;\n  }\n\n  *:focus + .popover-container,\n  &:hover .popover-container {\n    display: block;\n    opacity: 1;\n    transform: translate(-50%, -100%) scale(1);\n  }\n\n  &.popover-right {\n    .popover-container {\n      left: 100%;\n      top: 50%;\n    }\n\n    *:focus + .popover-container,\n    &:hover .popover-container {\n      transform: translate(0, -50%) scale(1);\n    }\n  }\n\n  &.popover-bottom {\n    .popover-container {\n      left: 50%;\n      top: 100%;\n    }\n\n    *:focus + .popover-container,\n    &:hover .popover-container {\n      transform: translate(-50%, 0) scale(1);\n    }\n  }\n\n  &.popover-left {\n    .popover-container {\n      left: 0;\n      top: 50%;\n    }\n\n    *:focus + .popover-container,\n    &:hover .popover-container {\n      transform: translate(-100%, -50%) scale(1);\n    }\n  }\n\n  .card {\n    @include shadow-variant(.2rem);\n    border: 0;\n  }\n}\n","// Steps\n.step {\n  display: flex;\n  flex-wrap: nowrap;\n  list-style: none;\n  margin: $unit-1 0;\n  width: 100%;\n\n  .step-item {\n    flex: 1 1 0;\n    margin-top: 0;\n    min-height: 1rem;\n    text-align: center;\n    position: relative;\n\n    &:not(:first-child)::before {\n      background: $primary-color;\n      content: \"\";\n      height: 2px;\n      left: -50%;\n      position: absolute;\n      top: 9px;\n      width: 100%;\n    }\n\n    a {\n      color: $primary-color;\n      display: inline-block;\n      padding: 20px 10px 0;\n      text-decoration: none;\n\n      &::before {\n        background: $primary-color;\n        border: $border-width-lg solid $light-color;\n        border-radius: 50%;\n        content: \"\";\n        display: block;\n        height: $unit-3;\n        left: 50%;\n        position: absolute;\n        top: $unit-1;\n        transform: translateX(-50%);\n        width: $unit-3;\n        z-index: $zindex-0;\n      }\n    }\n\n    &.active {\n      a {\n        &::before {\n          background: $light-color;\n          border: $border-width-lg solid $primary-color;\n        }\n      }\n\n      & ~ .step-item {\n        &::before {\n          background: $border-color;\n        }\n\n        a {\n          color: $gray-color;\n\n          &::before {\n            background: $border-color;\n          }\n        }\n      }\n    }\n  }\n}\n","// Tabs\n.tab {\n  align-items: center;\n  border-bottom: $border-width solid $border-color;\n  display: flex;\n  flex-wrap: wrap;\n  list-style: none;\n  margin: $unit-1 0 ($unit-1 - $border-width) 0;\n\n  .tab-item {\n    margin-t