de toast-variant($primary-color);\n  }\n\n  &.toast-success {\n    @include toast-variant($success-color);\n  }\n\n  &.toast-warning {\n    @include toast-variant($warning-color);\n  }\n\n  &.toast-error {\n    @include toast-variant($error-color);\n  }\n\n  a {\n    color: $light-color;\n    text-decoration: underline;\n    \n    &:focus,\n    &:hover,\n    &:active,\n    &.active {\n      opacity: .75;\n    }\n  }\n\n  .btn-clear {\n    margin: $unit-h;\n  }\n\n  p {\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n}\n","// Toast variant mixin\n@mixin toast-variant($color: $dark-color) {\n  background: rgba($color, .95);\n  border-color: $color;\n}\n","// Tooltips\n.tooltip {\n  position: relative;\n  &::after {\n    background: rgba($dark-color, .95);\n    border-radius: $border-radius;\n    bottom: 100%;\n    color: $light-color;\n    content: attr(data-tooltip);\n    display: block;\n    font-size: $font-size-sm;\n    left: 50%;\n    max-width: $control-width-sm;\n    opacity: 0;\n    overflow: hidden;\n    padding: $unit-1 $unit-2;\n    pointer-events: none;\n    position: absolute;\n    text-overflow: ellipsis;\n    transform: translate(-50%, $unit-2);\n    transition: opacity .2s, transform .2s;\n    white-space: pre;\n    z-index: $zindex-3;\n  }\n  &:focus,\n  &:hover {\n    &::after {\n      opacity: 1;\n      transform: translate(-50%, -$unit-1);\n    }\n  }\n  &[disabled],\n  &.disabled {\n    pointer-events: auto;\n  }\n\n  &.tooltip-right {\n    &::after {\n      bottom: 50%;\n      left: 100%;\n      transform: translate(-$unit-1, 50%);\n    }\n    &:focus,\n    &:hover {\n      &::after {\n        transform: translate($unit-1, 50%);\n      }\n    }\n  }\n\n  &.tooltip-bottom {\n    &::after {\n      bottom: auto;\n      top: 100%;\n      transform: translate(-50%, -$unit-2);\n    }\n    &:focus,\n    &:hover {\n      &::after {\n        transform: translate(-50%, $unit-1);\n      }\n    }\n  }\n  \n  &.tooltip-left {\n    &::after {\n      bottom: 50%;\n      left: auto;\n      right: 100%;\n      transform: translate($unit-2, 50%);\n    }\n    &:focus,\n    &:hover {\n      &::after {\n        transform: translate(-$unit-1, 50%);\n      }\n    }\n  }\n}\n","// Animations\n@keyframes loading {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes slide-down {\n  0% {\n    opacity: 0;\n    transform: translateY(-$unit-8);\n  }\n  100% {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n","// Background color utility mixin\n@mixin bg-color-variant($name: \".bg-primary\", $color: $primary-color) {\n  #{$name} {\n    background: $color !important;\n\n    @if (lightness($color) < 60) {\n      color: $light-color;\n    }\n  }\n}\n\n// Text color utility mixin\n@mixin text-color-variant($name: \".text-primary\", $color: $primary-color) {\n  #{$name} {\n    color: $color !important;\n  }\n\n  a#{$name} {\n    &:focus,\n    &:hover {\n      color: darken($color, 5%);\n    }\n    &:visited {\n      color: lighten($color, 5%);\n    }\n  }\n}\n","// Cursors\n.c-hand {\n  cursor: pointer;\n}\n\n.c-move {\n  cursor: move;\n}\n\n.c-zoom-in {\n  cursor: zoom-in;\n}\n\n.c-zoom-out {\n  cursor: zoom-out;\n}\n\n.c-not-allowed {\n  cursor: not-allowed;\n}\n\n.c-auto {\n  cursor: auto;\n}","// Display\n.d-block {\n  display: block;\n}\n.d-inline {\n  display: inline;\n}\n.d-inline-block {\n  display: inline-block;\n}\n.d-flex {\n  display: flex;\n}\n.d-inline-flex {\n  display: inline-flex;\n}\n.d-none,\n.d-hide {\n  display: none !important;\n}\n.d-visible {\n  visibility: visible;\n}\n.d-invisible {\n  visibility: hidden;\n}\n.text-hide {\n  background: transparent;\n  border: 0;\n  color: transparent;\n  font-size: 0;\n  line-height: 0;\n  text-shadow: none;\n}\n.text-assistive {\n  border: 0;\n  clip: rect(0,0,0,0);\n  height: 1px;\n  margin: -1px;\n  overflow: hidden;\n  padding: 0;\n  position: absolute;\n  width: 1px;\n}\n","// Divider\n.divider,\n.divider